      sodiumBicarbonate: { checked: false, note: 'Note for Sodium Bicarbonate' },
      chelodonium: true,
      germanium: true,
      amygdalin: true,
      helixor: true,
      curcumin: { checked: false, note: 'Note for Curcumin' },
      resveratrol: { checked: false, note: 'Note for Resveratrol' },
      quercetin: { checked: false, note: 'Note for Quercetin' },
      silymarin: true,
      bilberryExtract: true,
      EGCG: true,
      NAD: true,
      zinc: true,
      echinacheaFE: true,
      DRibose: true,
      glutamine: true,
